import React from 'react';
import { connect } from 'react-redux';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import { selectTasklist } from '../actions/';

const TaskLists = (props) => {
  let tasklists = [];
  for (let tasklistID in props.tasklists) {
    tasklists.push(
      <ListItem onClick={() => props.selectTasklist(tasklistID)} button key={tasklistID}>
        <ListItemText primary={props.tasklists[tasklistID].title} />
      </ListItem>
    );
  }
  return (
    <List>
      {tasklists}
    </List>
  );    
}

const mapStateToProps = (state) => {
  return {
    tasklists: state.tasklists
  };
};

const mapDispatchToProps = (dispatch) => ({
  selectTasklist(tasklistID) {
    dispatch(selectTasklist(tasklistID))
  }
});


export default connect(mapStateToProps, mapDispatchToProps)(TaskLists);


